import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { DataService } from './data.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable({
  providedIn: 'root'
})
export class CartService {

  constructor(
    private http: Http,
    private dataService: DataService
  ) { }

  private cartQuantitySource = new BehaviorSubject<number>(this.getCartQty());
  currentCartQuantity = this.cartQuantitySource.asObservable(); 

  backendUrl = "http://localhost:8080";

  changeCartQuantity(qty:number){
    this.cartQuantitySource.next(qty);
  }

  getCartQty(): number {
    if(localStorage.getItem('SessionId')){
      let sessionId = JSON.parse(localStorage.getItem('SessionId'));

      if(localStorage.getItem('Cart')){
        let cart = JSON.parse(localStorage.getItem('Cart'));
        return cart.tickets.length;
      }
    }
   return 0;
  }

  getCart(): any {
    let sessionId: string = "";
    if (localStorage.getItem("SessionId")) {
      sessionId = JSON.parse(localStorage.getItem("SessionId"));
    }
    return this.http.get(this.backendUrl + "/cart/" + sessionId);

  }

  createCart() {
    let cart: any = {};
    localStorage.setItem("cart", JSON.stringify(cart));
  }

  addToCart(qty: number, body: any): any {
    if(!localStorage.getItem("Cart")){
      localStorage.setItem("Cart","{}");
    }
    let cart = JSON.parse(localStorage.getItem("Cart"));
    if(!cart){
      cart.quantity = 0;
    }
    let fullCart: boolean = false;
    let sessionId = JSON.parse(localStorage.getItem("SessionId"));

    if(sessionId == null){
      this.dataService.initSessionId();
      sessionId = JSON.parse(localStorage.getItem("SessionId"));
    }

    if (cart.quantity + qty <= 6 || cart.quantity == undefined) {
      return this.http.put(this.backendUrl + "/cart/" + sessionId + "?qty=" + qty, body)
        .subscribe(res => {
          cart = res.json();
          this.changeCartQuantity(cart.tickets.length);
          localStorage.setItem("Cart", JSON.stringify(cart));
        });
    } else {
      fullCart = true;
      return fullCart;
    }

  }

  removeFromCart(ticket: any): any {
    let id = JSON.parse(localStorage.getItem("SessionId"));
    return this.http.patch(this.backendUrl + "/cart/remove/" + id, ticket)
      .subscribe(res => {
        let cart = res.json();
        localStorage.setItem("Cart", JSON.stringify(cart));
      });
  }


}
