import { Component, OnInit } from '@angular/core';
import { CartService } from '../cart.service';
import { DataService } from '../data.service';
import { TransactionService} from '../transaction.service';
import {Router} from "@angular/router";
import { FormsModule } from '@angular/forms';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-payment-page',
  templateUrl: './payment-page.component.html',
  styleUrls: ['./payment-page.component.scss']
})
export class PaymentPageComponent implements OnInit {

  constructor(
    private cartService: CartService,
    private dataService: DataService,
    private transactionService: TransactionService,
    private router:Router
  ) { }

  cart:any;

  todayDate: Date = new Date();
  todayAnnee: number = this.todayDate.getFullYear();
  todayMois: number = this.todayDate.getMonth()+1;

  cardValid:boolean;
  dateValid:boolean;
  nameValid:boolean;
  cvvValid:boolean;

  ccNumbers : any;   
  ccAnnee: any;
  ccMois: any;
  nameOnCard: any;
  cvv : any; 

  ngOnInit() {
    this.populateCart();
  }


  validateCC(){
    this.cardValid = false;
    this.dateValid = false;
    this.nameValid = false;
    this.cvvValid = false;
    
    //Valide credit card numbers with luhn algorithm.
    this.ccCardValid();
    //Validate credit card date.
    this.ccDateValid();
    //Validate name does not contain number(s).
    this.ccValidNameOnCard();
    //valide CVV is a number of lenght 3.
    this.ccValidateCVV();
  }
  
  ccValidateCVV(){
    if(typeof this.cvv !== 'undefined' && (this.cvv.length == 3 || this.cvv.length == 4)){
      if(Number(this.cvv) > 0){
        this.cvvValid = true;
      }
      else{
        this.cvvValid = false;
      }
    }
    else{
      this.cvvValid = false;
      console.log(this.cvv.length);
    }
  }

  ccValidNameOnCard(){
    if(typeof this.nameOnCard !== 'undefined'){
      if( /^[A-Za-z\s]+$/.test(this.nameOnCard) ){
        this.nameValid = true;
      }
      else{
        this.nameValid = false;
      }
    }    
    else{
      this.nameValid = false;
    }
  }

  //Validate credit card date.
  ccDateValid(){

    if(this.ccAnnee >  this.todayAnnee ||
      this.ccAnnee ==  this.todayAnnee && this.ccMois >= this.todayMois){
        this.dateValid = true;
      }
    else{
      this.dateValid = false;
    }
  }

  //Valide credit card numbers with luhn algorithm.
  ccCardValid(){
    if(typeof this.ccNumbers !== 'undefined'){
      if(this.ccLuhnValid(this.ccNumbers) && Object.keys(this.ccNumbers).length != 0){
        this.cardValid = true;
      }
      else if(!this.ccLuhnValid(this.ccNumbers)){
        this.cardValid = false;
      }
    }
    else{
      this.cardValid = false;
    }
  }

  //Source of luhn algorithm: https://github.com/theAlgorithmist/FormValidation
  ccLuhnValid(cardNumber: string): boolean {
    var digit: number;
    var n: number;
    var sum: number; 
    var j: number;
    sum = 0;
    var numbers:Array<number> = cardNumber.split('').reverse().map( (val) => parseFloat(val) );
    var len: number           = numbers.length;
    n = 0;
    j = 0;
    while( j < len ) 
    {
      digit = numbers[n];
      digit = +digit;
      if( n % 2 ) 
      {
        digit *= 2;
        if( digit < 10 )
          sum += digit;
        else
          sum += digit - 9;
      }
      else
       sum += digit;
      n = ++j;
    }
    return sum % 10 === 0;
  }

  saveCustomerInfo(paymentInfoForm:any){
    this.validateCC();

    console.log(paymentInfoForm, "here is your form");
    let form = paymentInfoForm.value;
    let requestBody:any = {};
    this.populateRequestBody(requestBody,form,this.cart);
    this.transactionService.createTransaction(requestBody)
    .subscribe(res =>{
      let response = res.json();
      if(response.status == 'P'){
        console.log('transaction created');
        this.router.navigate(['/payment-transaction-confirmation']);
      }
    });
  }

  populateRequestBody(requestBody:any, form:any, cart:any){
    requestBody.merchantKey = "8c9946a7-0300-4048-89da-214c28ab9f51";
    requestBody.cardLastName = form.lastName;
    requestBody.cartFirstName = form.firstName;
    requestBody.cartNumber = form.ccNumber;
    requestBody.cardExpiration = form.exp;
    requestBody.cvv = form.cvv;
    requestBody.amounth = cart.price;
  }

  populateCart(){
    this.cartService.getCart()
    .subscribe(res =>{
      let response = res.json();
      this.cart = response;
    });
  }

}
