import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomepageComponent} from './homepage/homepage.component';
import { LoginComponent } from './login/login.component';
import { CartPageComponent } from './cart-page/cart-page.component';
import { ProductlistComponent } from './productlist/productlist.component';
import { PaymentTransactionConfirmationComponent } from './payment-transaction-confirmation/payment-transaction-confirmation.component';
import { PaymentPageComponent } from './payment-page/payment-page.component';
import {Page404Component} from './page404/page404.component';
import { PaymentErrorPageComponent} from './payment-error-page/payment-error-page.component';
import { PaymentConfirmationComponent } from './payment-confirmation/payment-confirmation.component';

const routes: Routes = [
  { path: 'home', component: HomepageComponent },
  { path:'login', component: LoginComponent },
  { path:'payment', component: PaymentPageComponent },
  { path:'page404', component: Page404Component },
  { path:'productlist', component: ProductlistComponent },
  { path:'cart', component: CartPageComponent },
  { path:'payment-error', component: PaymentErrorPageComponent },
  { path:'payment-confirmation', component: PaymentConfirmationComponent },
  { path:'payment-info-confirmation', component: PaymentTransactionConfirmationComponent},
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  },
  {
    path: '**',
    redirectTo: '/page404',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
