import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../data.service';
import { CartService } from '../cart.service';
import { AppComponent } from '../app.component';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {

  search = "";

  constructor(
    private router: Router,
    private cartService: CartService,
    public dataService: DataService
  ) { }

  qty: number;

  ngOnInit() {
    this.updateCartQuantity();
    this.cartService.currentCartQuantity.subscribe(qty => this.qty = qty);
  }

  searchShows(searchFrom: NgForm){
    this.router.navigateByUrl("/productlist/"+ searchFrom.value.search);
  }

  updateCartQuantity(){
    if(JSON.parse(localStorage.getItem("Cart"))){
      this.qty = JSON.parse(localStorage.getItem("Cart")).quantity;
    }else{
      this.qty = 0;
    }
    
  }

}
