import { Component, OnInit } from '@angular/core';
import { CartService } from '../cart.service';

@Component({
  selector: 'app-cart-page',
  templateUrl: './cart-page.component.html',
  styleUrls: ['./cart-page.component.scss']
})
export class CartPageComponent implements OnInit {

  constructor(
    private cartService: CartService
  ) { }

  cart: any;
  cartPrice: number;
  cartQuantity:number;

  ngOnInit() {
    this.getCartData();
    this.cartService.currentCartQuantity.subscribe(qty => this.cartQuantity = qty);
  }

  getCartData() {
    this.cartService.getCart()
      .subscribe(res => {
        let response = res.json();
        this.cart = response;
        this.cartPrice = this.cart.price;
        this.getCartQuantity();
      });

  }

  removeTicket(ticket: any) {
    let index = this.cart.tickets.indexOf(ticket, 0);
    if (index > -1) {
      this.cart.tickets.splice(index, 1);
    }
    this.updateCartPrice(this.cart);
    this.updateCartQty(this.cart);
    this.getCartPrice();
    this.getCartQuantity();
    localStorage.setItem('Cart',JSON.stringify(this.cart))
    this.cartService.changeCartQuantity(this.cart.tickets.length);
    this.cartService.removeFromCart(ticket);
  }

  updateCartPrice(cart: any){
    let price:number = 0.0;
    cart.tickets.forEach(element => {
      price = price + element.price;
    });
    cart.price = price;
  }

  updateCartQty(cart: any){
    this.cart.quantity = this.cart.tickets.length;
  }

  getCartPrice(){
    let price:number = 0.0;
    this.cart.tickets.forEach(element => {
      price = price + element.price;
    });
    this.cartPrice = price;
  }

  getCartQuantity(){
    this.cartQuantity = this.cart.tickets.length;
  }

}
