import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { CartService } from '../cart.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-product-detail',
  templateUrl: './product-detail.component.html',
  styleUrls: ['./product-detail.component.scss']
})
export class ProductDetailComponent implements OnInit {

  showId: String;
  showData: any;
  displayError: boolean;
  quantity = 1;
  cartQuantity:number;

  constructor(
    private dataService: DataService,
    private cartService: CartService,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
   this.getShowData();
   if(!localStorage.getItem("SessionId")){
     this.initiateSessionId();
   }
  }

  getShowData(): any{
    this.route.params.subscribe(params =>{
      this.showId = params['sku'];
    });
    this.dataService.getShow(this.showId)
      .subscribe(res => {
        this.showData = res.json();
      });
  }

  addToCart(addToCartForm: any){
    let body = this.showId;
    let qty = addToCartForm.value.quantity;

    let res = this.cartService.addToCart(qty,body);
    if(res == true){
      this.displayError = true;
    }
  }

  initiateSessionId(){
    this.dataService.initSessionId();
  }
}
